@charset "UTF-8";
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*::-webkit-input-placeholder {
  color: #202020;
  opacity: 1; }

*:-moz-placeholder {
  color: #202020;
  opacity: 1; }

*::-moz-placeholder {
  color: #202020;
  opacity: 1; }

*:-ms-input-placeholder {
  color: #202020;
  opacity: 1; }

*,
*:before,
*:after {
  box-sizing: border-box;
  outline: none;
  padding: 0;
  margin: 0; }

::-webkit-scrollbar {
  width: 4px; }

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-border-radius: 0;
  border-radius: 0; }

::-webkit-scrollbar-thumb {
  -webkit-border-radius: 0;
  border-radius: 0;
  background: rgba(68, 68, 68, 0.8);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5); }

::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(255, 0, 0, 0.4); }

::-webkit-scrollbar-button {
  background: red; }

html {
  box-sizing: border-box;
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  /* overflow-x: hidden; */ }

body {
  font-family: "RobotoRegular", sans-serif;
  font-size: 16px;
  line-height: 1.42;
  position: relative;
  min-width: 320px;
  height: 100%;
  margin: 0;
  padding: 0;
  color: #000000;
  opacity: 1;
  scroll-behavior: smooth;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  -webkit-backface-visibility: hidden;
  -webkit-font-smoothing: antialised;
  overflow-x: hidden; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

a {
  outline: 0;
  text-decoration: none; }

ul {
  margin: 0;
  padding: 0; }

li {
  list-style-type: none; }

.img-responsive {
  display: block;
  width: 100%;
  height: auto; }

.hidden {
  display: none; }

