.checkbox {
  margin: 0;
  padding: 0;
  display: block;
  width: 100%;
  margin-bottom: 20px; }

.checkbox input[type="checkbox"] {
  margin-left: 0;
  padding: 0; }

.checkbox input[type="checkbox"].css-checkbox {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0px;
  border: 0; }

.checkbox input[type="checkbox"].css-checkbox + label.css-label {
  background-image: url("../images/checkbox_yes.png");
  padding-left: 22px;
  display: block;
  line-height: 17px;
  background-repeat: no-repeat;
  font-size: 13px;
  vertical-align: middle;
  cursor: pointer;
  opacity: 1;
  color: #cbcaca; }

.checkbox input[type="checkbox"].css-checkbox + label.css-label:hover {
  opacity: 1.0; }

.checkbox input[type="checkbox"]:checked + label.css-label {
  background-image: url("../images/checkbox_no.png"); }

.css-label a {
  color: #cbcaca;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease; }

.css-label a:hover {
  color: #b2b0b0; }

.calculator_box_item .checkbox input[type="checkbox"].css-checkbox + label.css-label {
