/* ниже размещено стили оформления наполнения шаблона в
зависимостиь от разрешения, в документ были вклюены все разрешения
не зависомо от требования заказчика  */
/* первое разрешение под казачика */
@media only screen and (max-width: 720px) {
  .mobile_gamburger {
    display: block; }
  .top_line_menu {
    display: none; }
  .header_content_item {
    width: 100%;
    float: none; }
  .header_img:before {
    display: none; }
  .header_content_item:last-child {
    width: 100%; }
  /*конец оформления кнопки  меню*/
  .top_mnu {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(40, 40, 38, 0.8);
    z-index: 50;
    right: 0;
    top: -100%;
    /* display: flex;
justify-content: center;
align-items: center; */ }
  /* .top_mnu.active {
top: 0;
} */
  .top_mnu ul {
    /*   margin-top: 120px; */
    text-align: center;
    width: 100%; }
  .top_mnu ul li {
    margin: 0;
    padding: 0; }
  .top_mnu ul li a {
    display: block;
    font-family: "RobotoMedium", sans-serif;
    text-align: center;
    font-size: 1.5rem;
    line-height: 1.2;
    padding: 20px;
    color: #fff;
    text-transform: uppercase;
    -moz-transition: all 0.2s ease;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease; }
  .top_mnu ul li a:hover {
    background-color: rgba(255, 255, 255, 0.4);
    color: #FF8941; }
  /*оформление скрытого меню*/
  .hidden-mnu {
    background-color: rgba(0, 0, 0, 0.7);
    display: none;
    min-width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1; }
  .hidden-mnu ul {
    margin: 0;
    padding: 0; }
  .hidden-mnu ul li {
    display: block;
    margin: 0;
    padding: 0; }
  .hidden-mnu ul li a {
    display: block;
    text-align: center;
    font-size: 1.6rem;
    text-transform: uppercase;
    text-decoration: none;
    line-height: 1.5;
    padding: 20px 0;
    color: #fff;
    -moz-transition: all 0.2s ease;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease; }
  .hidden-mnu ul li a:hover {
    /* background-color:rgba(40, 40, 38, 0.60); */
    color: #FF8941; }
  .top_line_box, .top_line {
    height: 80px; }
  /*Disable Animation on Mobile Devices*/
  .animated {
    /*CSS transitions*/
    -o-transition-property: none !important;
    -moz-transition-property: none !important;
    -ms-transition-property: none !important;
    -webkit-transition-property: none !important;
    transition-property: none !important;
    /*CSS transforms*/
    -o-transform: none !important;
    -moz-transform: none !important;
    -ms-transform: none !important;
    -webkit-transform: none !important;
    transform: none !important;
    /*CSS animations*/
    -webkit-animation: none !important;
    -moz-animation: none !important;
    -o-animation: none !important;
    -ms-animation: none !important;
    animation: none !important; } }

